import React, { memo } from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import { Form, FormProps, useField } from 'react-final-form';

type CreateFormData = {
  name: string;
  param1: string;
};

type Props = {
  open: boolean;
  onClose: () => void;
  onSubmit: (formData: CreateFormData) => void;
};

const NameField = () => {
  const { input } = useField('name', {});

  return (
    <TextField
      fullWidth
      label="Название балансировщика"
      margin="dense"
      name={input.name}
      onChange={input.onChange}
      size="small"
      value={input.value}
      variant="outlined"
    />
  );
};

const ParamField = () => {
  const { input } = useField('param1', {});

  return (
    <TextField
      fullWidth
      label="Параметр 1"
      margin="dense"
      name={input.name}
      onChange={input.onChange}
      size="small"
      value={input.value}
      variant="outlined"
    />
  );
};

export const CreateDialog = memo(({ open, onClose, onSubmit }: Props) => {
  return (
    <Dialog onClose={onClose} open={open}>
      <Form<CreateFormData> onSubmit={onSubmit}>
        {(props: FormProps) => (
          <form onSubmit={props.handleSubmit}>
            <DialogTitle>Создание балансировщика</DialogTitle>
            <DialogContent>
              <DialogContentText>Описание того, что нужно вводить для создания балансировщика.</DialogContentText>
              <NameField />
              <ParamField />
            </DialogContent>
            <DialogActions>
              <Button onClick={onClose}>Отмена</Button>
              <Button type="submit">Создать</Button>
            </DialogActions>
          </form>
        )}
      </Form>
    </Dialog>
  );
});
