import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { BALANCERS_REDUCER_NAME } from './constants';
import { BalancersState, BalancerList } from './types';

export const initialState: BalancersState = {
  balancerList: [],
  total: 0,
};

const slice = createSlice({
  name: BALANCERS_REDUCER_NAME,
  initialState,
  reducers: {
    setBalancerList: (state, { payload: balancerList }: PayloadAction<BalancerList>): BalancersState => ({
      ...state,
      balancerList,
    }),
    setBalancerTotal: (state, { payload: total }: PayloadAction<number>): BalancersState => ({
      ...state,
      total,
    }),
    getBalancersSaga: state => state,
  },
});

export const BalancersActions = slice.actions;

export default slice.reducer;
