import React, { useCallback, useState } from 'react';
import { Button, Typography } from '@mui/material';
import { Add } from '@mui/icons-material';
import { Search } from 'components/search';
import { Pagination } from 'components/pagination';
import { useSelector } from 'react-redux';
import { useLoadingProvider, LoadersDict } from 'components/loading-provider/';
import { CircularPreloader } from 'components/circular-preloader';
import { TableOfBalancers } from './components/table-of-balancer';
import styles from './index.module.scss';
import { CreateDialog } from './components/create-dialog';
import { balancerListSelector } from '../redux';

export const BalancersPage = () => {
  const [openDialog, setOpenDialog] = useState(false);

  const openDialogHandler = useCallback(() => {
    setOpenDialog(true);
  }, []);

  const closeDialogHandler = useCallback(() => {
    setOpenDialog(false);
  }, []);
  const submitDialogHandler = useCallback(() => {
    setOpenDialog(false);
  }, []);

  const balancerList = useSelector(balancerListSelector);
  const isLoading = useLoadingProvider(LoadersDict.GET_BALANCERS_TABLE);

  return (
    <>
      <Typography gutterBottom variant="h4">
        Балансировщики
      </Typography>
      <div className={styles.controls}>
        <Button onClick={openDialogHandler} startIcon={<Add />} variant="outlined">
          Добавить балансировщик
        </Button>
        <Search />
      </div>
      <div className={styles.body}>
        {isLoading && <CircularPreloader />}
        <TableOfBalancers data={balancerList} />
        <Pagination count={balancerList.length} />
      </div>
      <CreateDialog onClose={closeDialogHandler} onSubmit={submitDialogHandler} open={openDialog} />
    </>
  );
};
