import React from 'react';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import cn from 'classnames';
import styles from './index.module.scss';

type Host = {
  name: string;
  ip: string;
  flags: string;
  status: boolean;
  description: string;
};

type HostList = Array<Host>;

type Props = {
  data: HostList;
};

export const TableOfBalancers = ({ data }: Props) => {
  const bodyData = data.map(row => (
    <TableRow key={row.name}>
      <TableCell>{row.name}</TableCell>
      <TableCell>{row.ip}</TableCell>
      <TableCell>{row.flags}</TableCell>
      <TableCell>{row.status ? 'Активен' : 'Неактивен'}</TableCell>
      <TableCell>{row.description}</TableCell>
    </TableRow>
  ));

  return (
    <TableContainer>
      <Table className={cn(styles.root, styles['Mui-active'])}>
        <TableHead>
          <TableRow>
            <TableCell>Название</TableCell>
            <TableCell>IP</TableCell>
            <TableCell>Флаги</TableCell>
            <TableCell>Статус</TableCell>
            <TableCell>Описание</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>{bodyData}</TableBody>
      </Table>
    </TableContainer>
  );
};
