import React, { useMemo } from 'react';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import { useRoute, useRouter } from 'react-router5';
import { useLinkOnClick } from 'hooks/use-on-modified-click';
import styles from './index.module.scss';

type Props = {
  title: string;
  routeName: string;
  secondary?: boolean;
};

export const MenuItem = ({ title, routeName, secondary }: Props) => {
  const {
    route: { name },
  } = useRoute();
  const router = useRouter();

  const handleClick = useLinkOnClick(() => {
    router.navigate(routeName);
  }, [router]);

  const path = useMemo(() => router.buildPath(routeName), [routeName, router]);

  return (
    <ListItemButton component="a" href={path} onClick={handleClick} selected={routeName === name}>
      <ListItemText className={secondary ? styles.secondaryText : styles.primaryText} primary={title} />
    </ListItemButton>
  );
};
