import { put, call, takeLatest } from 'redux-saga/effects';
import { batchActions } from 'redux-batched-actions';
import { BalancerGetRequest } from 'api/requests/balancers-get';
import { snackbarActions } from 'redux/snackbar';
import { LoadersDict, loadingProviderActions } from 'components/loading-provider';
import { BalancersActions } from '../../slice';

function* worker() {
  try {
    yield put(loadingProviderActions.enable(LoadersDict.GET_BALANCERS_TABLE));
    const { data, isError } = yield call(BalancerGetRequest);
    if (isError) {
      throw new Error(`Не удалось не удалось получить список балансировщиков`);
    }
    yield put(batchActions([BalancersActions.setBalancerList(data.balancerList)]));
  } catch (error) {
    yield put(snackbarActions.error(error.message));
  } finally {
    yield put(loadingProviderActions.disable(LoadersDict.GET_BALANCERS_TABLE));
  }
}

export function* watcher() {
  yield takeLatest(BalancersActions.getBalancersSaga, worker);
}
