import { api } from 'api/config';
import { RequestConfig } from 'api/types';
import * as s from 'superstruct';
import { errorsSchema } from 'api/utils/error-schema';

// -------------------------MOCK------------------------------------//
// -----------------------------------------------------------------//
const MOCK_DATA_COUNT = 14;

const getMockItem = (id: number) => ({
  name: `name_${id}`,
  ip: `ip_${id}`,
  port: 3000 + id,
  description: `Описание ${id}`,
});

const getMockData = (count: number) => {
  const balancers = [];
  for (let i = 0; i < count; i += 1) {
    balancers.push(getMockItem(i));
  }

  return { balancerList: balancers };
};
export const data = getMockData(MOCK_DATA_COUNT);
// -----------------------------------------------------------------//
// -----------------------------------------------------------------//

const validationSchema = s.partial(
  s.object({
    ...errorsSchema,
    balancerList: s.array(
      s.object({
        name: s.string(),
        ip: s.string(),
        port: s.number(),
        description: s.string(),
      }),
    ),
  }),
);

export type GetBalancersResponse = s.Infer<typeof validationSchema>;

const delay = (ms: number) => new Promise(res => setTimeout(res, ms));
const config: RequestConfig = {
  method: 'GET',
  url: 'https://jsonplaceholder.typicode.com/posts/1',
  validationSchema,
  /*  validate: data => {
    s.assert(data, validationSchema);
  }, */
  mock: async () => {
    await delay(2000);

    return getMockData(MOCK_DATA_COUNT);
  },
};

export const BalancerGetRequest = () => api.request(config);
