import React from 'react';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import cn from 'classnames';
import styles from './index.module.scss';

type Domain = {
  name: string;
  description: string;
  vsList: Array<string>;
};

type DomainList = Array<Domain>;

type Props = {
  data: DomainList;
};

export const TableOfDomains = ({ data }: Props) => {
  const bodyData = data.map(row => (
    <TableRow key={row.name}>
      <TableCell>{row.name}</TableCell>
      <TableCell>{row.description}</TableCell>
      <TableCell>{row.vsList.join(' , ')}</TableCell>
    </TableRow>
  ));

  return (
    <TableContainer>
      <Table className={cn(styles.root, styles['Mui-active'])}>
        <TableHead>
          <TableRow>
            <TableCell>Название</TableCell>
            <TableCell>Описание</TableCell>
            <TableCell>Список VS</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>{bodyData}</TableBody>
      </Table>
    </TableContainer>
  );
};
