import { createSelector } from 'reselect';
import { BalancersStatePart, BalancersState, BalancerList } from './types';
import { BALANCERS_REDUCER_NAME } from './constants';
import { initialState } from './slice';

export const balancersSelector = (state: BalancersStatePart): BalancersState =>
  state[BALANCERS_REDUCER_NAME] || initialState;

export const balancerListSelector = createSelector(
  [balancersSelector],
  ({ balancerList }: BalancersState): BalancerList => balancerList,
);

export const totalSelector = createSelector([balancersSelector], ({ total }: BalancersState): number => total);
