const MOCK_DATA_COUNT = 14;

const getMockItem = (id: number) => ({
  name: `name_${id}`,
  description: `Описание ${id}`,
  vsList: [`vs_${id}_1`, `vs_${id}_2`, `vs_${id}_3`],
});

const getMockData = (count: number) => {
  const result = [];
  for (let i = 0; i < count; i += 1) {
    result.push(getMockItem(i));
  }

  return result;
};
export const data = getMockData(MOCK_DATA_COUNT);
