import * as s from 'superstruct';
import { RequestConfig, Query } from './types';
import { queryParamsObjToStr } from './utils/queryParamsObjToString';

class API {
  public middleware: Array<(query: Query) => void> = [];

  public validate: (data: any, schema: any) => void = () => {
    // eslint-disable-next-line no-console
    console.warn('Схема валидации данных не задана');
  };

  async request(config: RequestConfig): Promise<Query> {
    const query: Query = {
      request: config,
      response: null,
      data: null,
      code: 0,
      isError: false,
      errorText: '',
      errorRawData: null,
    };

    try {
      const { method, url, queryParams, headers = {}, body, validationSchema, validate, mock } = config;
      const params = queryParamsObjToStr(queryParams);
      if (body) {
        headers['Content-Type'] = 'application/json;charset=utf-8';
      }

      if (mock) {
        query.data = await mock();
        query.code = 200;
        query.isError = false;
      } else {
        query.response = await fetch(url + params, {
          method,
          headers,
          body,
        });

        query.data = await query.response.json();
        query.code = query.response.status;
        query.isError = !query.response.ok;
      }

      try {
        if (validate) {
          await validate(query.data);
        } else if (validationSchema) {
          await this.validate(query.data, validationSchema);
        }
      } catch (validationError) {
        query.isError = true;
        query.errorText = `${validationError}`;
        query.errorRawData = validationError;

        return query;
      }

      query.errorText = query.isError ? query.data.errorMsg : '';

      return query;
    } catch (err) {
      query.errorRawData = err;
      query.isError = true;
      query.errorText = `${err}`;

      return query;
    } finally {
      this.middleware.forEach(mdl => mdl(query));
    }
  }
}

const api = new API();
api.validate = (data, schema) => s.assert(data, schema);

api.middleware.push(query => {
  // eslint-disable-next-line no-console
  console.log(query);
});

export { api };
