import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useRoute } from 'react-router5';
import { withInject } from 'store/withInject';
import { BalancersPage } from './page';
import BalancersReducer, { BALANCERS_REDUCER_NAME, getBalancerListSaga, BalancersActions } from './redux';

const Page = () => {
  const dispatch = useDispatch();
  const { route } = useRoute();
  useEffect(() => {
    dispatch(BalancersActions.getBalancersSaga());
  }, [dispatch, route.params.limit, route.params.page]);

  return <BalancersPage />;
};

export default withInject({
  reducers: { [BALANCERS_REDUCER_NAME]: BalancersReducer },
  sagas: { getBalancerListSaga: getBalancerListSaga.watcher },
})(Page);
