import React from 'react';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import cn from 'classnames';
import styles from './index.module.scss';

type VirtualService = {
  ip: string;
  port: number;
  protocol: string;
  weight: number;
  flags: string;
  description: string;
};

type VirtualServiceList = Array<VirtualService>;

type Props = {
  data: VirtualServiceList;
};

export const TableOfBalancers = ({ data }: Props) => {
  const bodyData = data.map(row => (
    <TableRow key={row.ip}>
      <TableCell>{row.ip}</TableCell>
      <TableCell>{row.port}</TableCell>
      <TableCell>{row.protocol}</TableCell>
      <TableCell>{row.weight}</TableCell>
      <TableCell>{row.flags}</TableCell>
      <TableCell>{row.description}</TableCell>
    </TableRow>
  ));

  return (
    <TableContainer>
      <Table className={cn(styles.root, styles['Mui-active'])}>
        <TableHead>
          <TableRow>
            <TableCell>IP</TableCell>
            <TableCell>Порт</TableCell>
            <TableCell>Протокол</TableCell>
            <TableCell>Вес</TableCell>
            <TableCell>Флаги</TableCell>
            <TableCell>Описание</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>{bodyData}</TableBody>
      </Table>
    </TableContainer>
  );
};
