import React, { useMemo } from 'react';
import { Link } from '@mui/material';
import { useLinkOnClick } from 'hooks/use-on-modified-click';
import { useRouter } from 'react-router5';
import cn from 'classnames';
import styles from './index.module.scss';

type Props = {
  className?: string;
};

export const Logo = ({ className }: Props) => {
  const router = useRouter();

  const handleClick = useLinkOnClick(() => {
    router.navigateToDefault();
  }, [router]);

  const defaultPath = useMemo(() => {
    const { defaultRoute } = router.getOptions();
    if (defaultRoute) {
      return router.buildPath(defaultRoute);
    }
  }, [router]);

  return (
    <Link
      className={cn(styles.logo, className, styles['Mui-active'])}
      // color="inherit"
      href={defaultPath}
      onClick={handleClick}
      tabIndex={0}
      underline="none"
      variant="h5"
    >
      WB Balancer
    </Link>
  );
};
